/**************************************************************************
 * MIXINS > POSITION
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    position: {
      type: String,
      default: "bottom-left",
      validator(x) {
        return [
          "bottom-left",
          "bottom-right",
          "top-left",
          "top-right"
        ].includes(x)
      }
    }
  }
}
