/**************************************************************************
 * FILTERS > TEXT
 ***************************************************************************/

/**
 * Truncate a string to a given length with a clamp
 * @param {String} text
 * @param {Number} length
 * @param {String} clamp
 * @return {String} The truncated sling with clamp
 */
export function truncate(text, length, clamp) {
  text = text || ""
  clamp = clamp || "..."
  length = length || 30

  if (text.length <= length) {
    return text
  }

  let truncated = text.slice(0, length - clamp.length)
  let last = truncated.length - 1

  while (last > 0 && truncated[last] !== " " && truncated[last] !== clamp[0]) {
    last -= 1
  }

  // Fix for case when text dont have any `space`
  last = last || length - clamp.length

  truncated = truncated.slice(0, last)

  return truncated + clamp
}
