<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  @click="onClick"
  :class=`[
    "c-instagram-stat",
    "c-instagram-stat--" + theme
  ]`
)
  span.c-instagram-stat__value {{ formattedValue }}

  span.c-instagram-stat__type {{ type }}
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import numeral from "numeral"

// PROJECT: MIXINS
import ThemeMixin from "@/mixins/ThemeMixin"

export default {
  mixins: [ThemeMixin],

  props: {
    abbreviate: {
      type: Boolean,
      default: false
    },
    separator: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      required: true
    },
    value: {
      type: Number,
      required: true
    }
  },

  computed: {
    formattedValue() {
      if (this.abbreviate && this.value >= 1000) {
        return numeral(this.value).format("0.0a")
      } else if (this.separator) {
        return numeral(this.value).format("0,0")
      }

      return this.value
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onClick() {
      this.$emit("click", this.type)
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-instagram-stat";

#{$c} {
  text-align: center;
  cursor: pointer;

  #{$c}__value,
  #{$c}__type {
    display: block;
  }

  #{$c}__value {
    margin-bottom: 3px;
    font-weight: 600;
    font-size: 13px;
    line-height: 13px;
  }

  #{$c}__type {
    text-transform: capitalize;
    font-size: 12px;
    line-height: 12px;
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__value {
        color: mdg($theme, "fonts", "default", "primary");
      }

      #{$c}__type {
        color: mdg($theme, "fonts", "default", "secondary");
      }
    }
  }
}
</style>
