<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
transition-group(
  :appear="true"
  :class=`[
    "c-instagram",
    "c-instagram--" + position,
    "c-instagram--" + theme,
    "u-no-drag",
    "u-reset-widget"
  ]`
  enter-active-class="u-animated u-ms500 u-fade-in-left"
  leave-active-class="u-animated u-ms250 u-fade-out-left"
  tag="div"
)
  widget-close(
    v-if="closed === false"
    @close="onClose"
    class="c-instagram__close"
    key="close"
    theme="light"
  )

  div(
    v-if="closed === false"
    class="c-instagram__widget"
    key="widget"
  )
    .c-instagram__header
      img(
        @click="onGoToInstagram"
        :src="profile.picture"
        class="c-instagram__picture"
      )

      .c-instagram__information
        .c-instagram__account
          span(
            @click="onGoToInstagram"
            class="c-instagram__name"
          ) {{ profile.name }}

          span(
            v-if="displayVerified"
            class="c-instagram__verified"
          )

        p(
          v-if="displayBio && profile.bio"
          v-html="formattedBio"
          class="c-instagram__bio"
        )

        instagram-follow(
          @click="onGoToInstagram"
          class="c-instagram__follow u-animated u-zoom-in u-ms250 u-delay250"
          size="small"
        ) Follow us on Instagram

    div(
      v-if="displayStats"
      class="c-instagram__stats"
    )
      instagram-stat(
        v-for="(stat, type) in profile.stats"
        @click="onGoToInstagram"
        :abbreviate="stat.abbreviate"
        :separator="stat.separator"
        :key="type"
        :theme="theme"
        :type="type"
        :value="stat.value"
        class="c-instagram__stat"
      )

    instagram-stories(
      v-if="displayStories && profile.stories.length > 0"
      :stories="profile.stories"
      :theme="theme"
      class="c-instagram__stories"
    )

    instagram-posts(
      v-if="displayPosts && profile.posts.length > 0"
      :posts="profile.posts"
      :theme="theme"
      class="c-instagram__posts"
    )

    widget-branding(
      :theme="theme"
      class="c-instagram__branding"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: COMPONENTS
import InstagramFollow from "@/components/instagram/InstagramFollow"
import InstagramPosts from "@/components/instagram/InstagramPosts"
import InstagramStat from "@/components/instagram/InstagramStat"
import InstagramStories from "@/components/instagram/InstagramStories"
import WidgetBranding from "@/components/widgets/WidgetBranding"
import WidgetClose from "@/components/widgets/WidgetClose"

// PROJECT: FILTERS
import { truncate } from "@/filters/text"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/CloseMixin"
import PositionMixin from "@/mixins/PositionMixin"
import ThemeMixin from "@/mixins/ThemeMixin"
import WidgetMixin from "@/mixins/WidgetMixin"

export default {
  components: {
    InstagramFollow,
    InstagramPosts,
    InstagramStat,
    InstagramStories,
    WidgetBranding,
    WidgetClose
  },

  mixins: [CloseMixin, PositionMixin, ThemeMixin, WidgetMixin],

  props: {
    displayBio: {
      type: Boolean,
      default: true
    },
    displayStats: {
      type: Boolean,
      default: false
    },
    displayStories: {
      type: Boolean,
      default: false
    },
    displayPosts: {
      type: Boolean,
      default: true
    },
    displayVerified: {
      type: Boolean,
      default: true
    }
  },

  data: () => ({
    // --> STATE <--

    widgetId: "gb_instagram",

    profile: {
      username: "lacoste",
      name: "Lacoste",
      bio: "Discover #LacosteHolidays",
      picture:
        "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-19/s320x320/61157095_819558548412483_4024847121898274816_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&oh=dd62b370cc03022b24e7d2ac03b1a34f&oe=5E7116CA",
      stats: {
        posts: {
          value: 2934,
          separator: true
        },
        followers: {
          value: 4537839,
          abbreviate: true
        },
        following: {
          value: 324
        }
      },
      stories: [
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.12442-15/e35/c65.171.594.594a/s150x150/60038549_461941087912873_2502133194004431667_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=101&oh=4e0d02ed8f6d1ea26e645458324af573&oe=5DF90D54"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.12442-15/e15/c189.211.336.336a/s150x150/50527502_1578625042240396_6507173641527973273_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=104&oh=c3ca64800ef86b7fd22009e067c8ed17&oe=5DF8F000"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.12442-15/e35/c57.456.662.662a/s150x150/59373901_133978197750219_899083304541495860_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=103&oh=0525d9bf51142a8e9e35525ec6dd7874&oe=5DF910C9"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.12442-15/e35/c30.322.690.690a/s150x150/70138699_1417426215090497_248935826207748205_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=105&oh=9ab381515540b3ba2ce380078991d9c8&oe=5DF94458"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.12442-15/e35/c257.677.471.471a/s150x150/69750433_610719402668380_980069722057269466_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=104&oh=e70a08038ee21bcac610a201062ea661&oe=5DF92BFE"
        }
      ],
      posts: [
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/73533217_151164999519846_3110215347054992841_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=106&oh=cd1522bf7c9ec22a13ed600bde5fce6b&oe=5DF8C879",
          type: "IMAGE"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/75640819_151648159473255_6585267910430448909_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=103&oh=f4c211a7a1d6725b06b3cfe714b7002f&oe=5DF8CE9A",
          type: "IMAGE"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/74911518_579589862807585_6659424057285305950_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=105&oh=fdf3afec80af8c016d7159e0c3f5ad64&oe=5DF9566D",
          type: "VIDEO"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/75238463_559151001591683_902648524925622335_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=111&oh=d5b65c053263221427774440f7b9ea13&oe=5DF8C677",
          type: "IMAGE"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/72262165_1200153056848335_3571263428856719537_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=103&oh=e298c110378dbc35d07b4c54fac600f7&oe=5DF8F3B7",
          type: "CAROUSEL_ALBUM"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/73232606_2407049072958617_3130107720465027570_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=105&oh=5cb09cc1d2b9f46577c4fbd80a92650f&oe=5DF92D15",
          type: "VIDEO"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/73407420_944881572578629_1641526920317995993_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=101&oh=24be85c63e03169edea15259ea89ff38&oe=5DF94397",
          type: "IMAGE"
        },
        {
          picture:
            "https://scontent-cdg2-1.cdninstagram.com/v/t51.2885-15/sh0.08/e35/s640x640/75388604_100734958037418_4983475422289251762_n.jpg?_nc_ht=scontent-cdg2-1.cdninstagram.com&_nc_cat=106&oh=62a79e21c4e345a5a9401897a99b88cf&oe=5E7D8E2C",
          type: "VIDEO"
        }
      ]
    }
  }),

  computed: {
    formattedBio() {
      // Wrap each hashtag with a link
      let bio = truncate(this.profile.bio, 55)

      bio = bio.replace(
        /(^|\W)(#[a-z\d][\w-]*)/gi,
        '$1<a class="c-instagram__hashtag" href="https://www.instagram.com/explore/tags/$2/" target="_blank">$2</a>'
      )

      // Replace # character in hrefs
      bio = bio.replace(/\/tags\/#/g, "/tags/")

      return bio
    }
  },

  methods: {
    onGoToInstagram() {
      window.open(
        `https://www.instagram.com/${this.profile.username}`,
        "_blank"
      )
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-instagram";
$margin: 10px;

#{$c} {
  position: absolute;
  margin: $margin;
  width: calc(100% - 2 * #{$margin});

  #{$c}__close {
    position: absolute;
  }

  #{$c}__widget {
    overflow: hidden;
    border-width: 1px;
    border-style: solid;
    border-radius: 6px;

    #{$c}__header,
    #{$c}__stats,
    #{$c}__stories,
    #{$c}__posts,
    #{$c}__branding {
      padding-right: 10px;
      padding-left: 10px;
    }

    #{$c}__stats,
    #{$c}__stories,
    #{$c}__posts,
    #{$c}__branding {
      border-top-width: 1px;
      border-top-style: solid;
    }

    #{$c}__header {
      display: flex;
      padding-top: 12px;
      padding-bottom: 12px;

      #{$c}__picture {
        flex: 0 0 auto;
        margin-right: 15px;
        width: 50px;
        height: 50px;
        border-width: 1px;
        border-style: solid;
        border-radius: 100%;
        user-select: none;
        cursor: pointer;
      }

      #{$c}__information {
        flex: 1;

        #{$c}__account {
          display: flex;
          align-items: center;

          #{$c}__name {
            font-weight: 600;
            font-size: 14px;
            line-height: 14px;
            cursor: pointer;

            &:hover {
              text-decoration: underline;
            }
          }

          #{$c}__verified {
            display: inline-block;
            margin-left: 5px;
            width: 14px;
            height: 14px;
            background-image: url(data:image/png;base64,);
            background-size: cover;
          }
        }

        #{$c}__bio {
          overflow: hidden;
          margin: 5px 0 0;
          max-height: 34px;
          font-size: 13px;
          line-height: 17px;

          #{$c}__hashtag {
            text-decoration: none;
            transition: color 125ms linear;
            cursor: pointer;

            &:hover {
              text-decoration: underline;
            }
          }
        }

        #{$c}__follow {
          margin-top: 9px;
        }
      }
    }

    #{$c}__stats {
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(3, 1fr);
      padding-top: 9px;
      padding-bottom: 9px;
    }

    #{$c}__branding {
      padding-top: 9px;
      padding-bottom: 9px;
      transition: color 125ms linear;
    }
  }

  // --> POSITIONS <--

  &--bottom-left {
    bottom: 0;
    left: 0;

    #{$c}__close {
      top: -25px;
      left: 0;
    }
  }

  &--bottom-right {
    right: 0;
    bottom: 0;

    #{$c}__close {
      top: -25px;
      right: 0;
    }
  }

  &--top-left {
    top: 0;
    left: 0;

    #{$c}__close {
      bottom: -25px;
      left: 0;
    }
  }

  &--top-right {
    top: 0;
    right: 0;

    #{$c}__close {
      right: 0;
      bottom: -25px;
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__widget {
        border-color: mdg($theme, "borders", "default", "primary");
        background-color: mdg($theme, "backgrounds", "default", "primary");
        color: mdg($theme, "fonts", "default", "primary");

        #{$c}__stats,
        #{$c}__stories,
        #{$c}__posts,
        #{$c}__branding {
          border-top-color: mdg($theme, "borders", "default", "primary");
        }

        #{$c}__header {
          #{$c}__picture {
            border-color: mdg($theme, "borders", "default", "primary");

            @if ($themeName == "dark") {
              box-shadow: 0
                1px
                5px
                mdg($theme, "shadows", "default", "primary");
            }
          }

          #{$c}__information {
            #{$c}__bio {
              color: mdg($theme, "fonts", "default", "secondary");

              #{$c}__hashtag {
                color: mdg($theme, "fonts", "default", "secondary");

                &:hover {
                  color: mdg($theme, "fonts", "default", "primary");
                }
              }
            }

            #{$c}__follow {
              @if ($themeName == "dark") {
                box-shadow: 0
                  1px
                  5px
                  mdg($theme, "shadows", "default", "primary");
              }
            }
          }
        }

        #{$c}__stats,
        #{$c}__branding {
          background-color: mdg($theme, "backgrounds", "default", "tertiary");
        }
      }
    }
  }
}

// --> BREAKPOINT: MOBILE WIDE <--

@include mq($from: mobileWide) {
  #{$c} {
    width: 280px;
  }
}
</style>
