/**************************************************************************
 * MIXINS > EDITOR
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  data: () => ({
    // --> STATE <--

    closed: null
  }),

  mounted() {
    // Hide widget when it has already been closed
    this.closed = !!localStorage.getItem(`${this.widgetId}_closed`)
  },

  methods: {
    // --> EVENT LISTENERS <--

    onClose() {
      if (this.editor) {
        this.$emit("alert", "Unclosable in editor mode")
      } else {
        this.closed = true

        localStorage.setItem(`${this.widgetId}_closed`, true)
      }
    }
  }
}
