<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
button(
  @click="onClick"
  :class=`[
    "c-instagram-follow",
    "c-instagram-follow--" + size,
    {
      "c-instagram-follow--full-width": fullWidth
    }
  ]`
)
  span.c-instagram-follow__inner
    slot
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
export default {
  props: {
    fullWidth: {
      type: Boolean,
      default: true
    },
    size: {
      type: String,
      default: "default",
      validator(x) {
        return ["small", "default"].includes(x)
      }
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onClick(event) {
      this.$emit("click", event)
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-instagram-follow";

#{$c} {
  display: inline-block;
  overflow: hidden;
  padding: 0;
  outline: 0;
  border: 0;
  border-radius: 3px;
  color: #ffffff;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 600;
  user-select: none;
  cursor: pointer;

  @include no-tap-highlight-color;

  #{$c}__inner {
    display: inline-block;
    box-sizing: border-box;
    padding: 0 10px;
    width: 100%;
    height: 100%;
    border: 1px solid #3897f0;
    background: #3897f0;
  }

  // --> SIZES <--

  &--small {
    font-size: 12px;
    line-height: 25px;
  }

  &--default {
    font-size: 14px;
    line-height: 26px;
  }

  // --> BOOLEANS <--

  &--full-width {
    width: 100%;
  }

  // --> INTERACTIONS <--

  &:active {
    #{$c}__inner {
      opacity: 0.7;
    }
  }
}

// --> BREAKPOINT: TABLET <--

@include mq($from: tablet) {
  #{$c} {
    #{$c}__inner {
      padding: 0 20px;
    }
  }
}
</style>
